@import "src/styles/variables";

.table-container {
  position: absolute;
  top: 20%;
  left: 20%;
  width: 70em;
}

.table{
  border: 1px solid #eee;
  table-layout: fixed;
  width: 100%;
  margin-bottom: 20px;

  &__button {
    position: relative;
    left: 46%;
    margin: 20px 0 50px 0;
    background-color: $base-yellow-color;
    font-size: 18px;
    font-weight: 600;
    padding: 10px 20px;
  }
}
.table th {
  font-weight: bold;
  padding: 5px;
  background: #efefef;
  border: 1px solid #dddddd;
}
.table td{
  padding: 5px 10px;
  border: 1px solid #eee;
  text-align: left;
}
.table tbody tr:nth-child(odd){
  background: #fff;
}
.table tbody tr:nth-child(even){
  background: #F7F7F7;
}
.table-title {
  text-align: center;
  font-size: 28px;
  font-weight: 600;
  padding-bottom: 30px;
}

.table-notfound {
  text-align: center;
}
